import React from 'react';
import './AccountPassword.scss';
import ReCAPTCHA from 'react-google-recaptcha';
import { Field, reduxForm } from 'redux-form';
import _ from 'lodash';
import config from 'Config';
import Header from '../../Header/components/Header.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';
import notRobotImg from '../assets/not-robot-img.png';

class AccountPasswordComponent extends React.Component {
    constructor(props) {
        super(props);
        const { handleSubmit } = this.props;
    }

    handleSubmit = values => {
      values.preventDefault();
      const { touch} = this.props;
      const errors= validate(values);
      if(errors){
        _.forEach(errors, (msg,field) => {
          touch(field);
        });
      }
    }
    renderRecaptchaField(field) {
      const { meta: { touched, error } } = field;
      return (
        <div>
          <ReCAPTCHA
            sitekey={config.ReCAPTCHA_KEY}
            onChange={field.input.onChange}
          />
          <div><p className="text-danger">{ touched ? error : '' }</p></div>
        </div>
      );
    }

    renderField = ({input,label,type,placeholder, meta: { touched, error, warning }}) => (
      <div className="form-group">
        <label htmlFor="rePass">{label}</label>
        <input {...input} className="form-control" placeholder={placeholder} type={type} />
        {touched && error && <span className="error-msg-text">{error}</span>}

      </div>
  )

    onChange = () => {
    }

    onRecaptcha = () => {}

    goToOrderSummary = () => {
        this.props.router.push('/order-summary');
    }

    goToLogin = () => {
      this.props.router.push('/login');
    }

    goToIndex = () => {
        this.props.router.push('/');
    }

    render() {
        return (
            <div className="account-password">
                <Header goHome={this.goToIndex} login={this.goToLogin} />
                <div className="progress">
                    <div className="finish-progress"></div>
                </div>

                <div className="container history-container">

                    <div className="row">
                        <div className="offset-1 col-10">
                            <div className="step-section">
                                <h4 className="step-text">Your Droplet Account Password</h4>
                            </div>
                            <div className="password-section">
                            <form onSubmit={this.handleSubmit}>
                              <div className="form-group">
                                <Field className="form-control" placeholder="**********" label="New Password" name="password" component={this.renderField} type="password" />
                              </div>
                              <div className="form-group">
                                <Field className="form-control" placeholder="**********" label="Retype New Password" name="secondPassword" component={this.renderField} type="password" />
                              </div>
                              <div>
                                  <Field name="recaptcha" component={this.renderRecaptchaField} />
                              </div>
                              <button className="btn btn-green btn-checkout" onClick={this.goToOrderSummary}>STEP 3 : ORDER SUMMARY ➡ </button>
                            </form>


                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


const validate = values => {
  const errors = {}
  if (!values.password) {
    errors.password = 'Required'
  }
  if (!values.secondPassword) {
    errors.secondPassword = 'Required';
  }
  if(values.password && values.secondPassword && (values.password != values.secondPassword)){
    errors.password = "Both the passwords do not match";
    errors.secondPassword = "Both the passwords do not match";
  }
  return errors
}

export default reduxForm({form: 'setPassword',validate})(AccountPasswordComponent)



